import React, { FC } from "react";
import {
  Modal,
  ModalProps,
  Form,
  type FormProps,
  Input,
  Button,
  message,
} from "antd";
import { PAGETYPENAME } from "@src/core/enums/access.enums";

interface IModifyNameDialog extends ModalProps {
  onClose: () => void;
  // 
  name: string;
  // 
  pageType: PageType;
  // 
  modifyNameSuccessHander?: (name: string) => void;
}

type FieldType = {
  name: string;
};

const ModifyNameDialog: FC<IModifyNameDialog> = ({
  name,
  open,
  onClose,
  pageType,
  modifyNameSuccessHander,
}) => {
  console.log(pageType, "pageType");
  const onFinish: FormProps<FieldType>["onFinish"] = (values) => {
    modifyNameSuccessHander?.(values.name);
    message.success("");
    onClose();
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      onCancel={onClose}
      footer={null}
      title={`${name ? "" : ""}${PAGETYPENAME[pageType]}`}
      destroyOnClose
    >
      <Form
        onFinish={onFinish}
        initialValues={{ name: name || "" }}
        autoComplete="off"
      >
        <Form.Item<FieldType>
          name="name"
          rules={[
            { required: true, message: `${PAGETYPENAME[pageType]}` },
          ]}
        >
          <Input placeholder={`${PAGETYPENAME[pageType]}`} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit" block>
            
          </Button>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default ModifyNameDialog;
